import React, { useState } from 'react';
import Table from 'react-bootstrap/Table';
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import Papa from 'papaparse';

export default function LotteryRcc() {
  const [csvData, setCsvData] = useState([]);
  const [winners, setWinners] = useState([]);

  const handleFileUpload = (event) => {
    const file = event.target.files[0];
    const reader = new FileReader();
    reader.onload = () => {
      const results = Papa.parse(reader.result, {
        header: true,
        dynamicTyping: true,
      });
      setCsvData(results.data);
    };
    reader.readAsText(file);
  };

  const handleRandomSelection = () => {
    const shuffledData = csvData.sort(() => 0.5 - Math.random());
    const selectedWinners = shuffledData.slice(0, 1);
    setWinners(selectedWinners);
  };

  return (
    <div>
      <Table striped bordered hover variant="dark">
        <thead>
         <tr>
            <th>Nro</th>
            <th>Nombre</th>
            <th>Cédula</th>
            <th>Numero de Teléfono</th>
            <th>Zona</th>
          </tr>
        </thead>
       <tbody>
         {winners.map((winner) => (
           <tr key={winner.nro}> 
              <td>{winner.nro}</td>
              <td>{winner.nombre}</td>
              <td>{winner.cedula}</td>
              <td>{winner.telefono}</td>
              <td>{winner.zona}</td>
           </tr>
          ))}
        </tbody>
      </Table>
      <Form.Group controlId="formFile" className="mb-3">
        <Form.Control type="file" onChange={handleFileUpload} />
        <Button variant="success" onClick={handleRandomSelection}>Realizar Sorteo</Button>
      </Form.Group>
    </div>
    
  );
}